body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  background-color: #160a2b;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

form {
  width: 100%;
}

.App {
  padding: 80px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

img {
  max-width: 100%;
}

.logo {
  width: 260px;
}

.search_input {
  width: 50%;
  padding: 16px 12px;
  font-size: 15px;
  margin: 60px 0;
}

input:focus {
  outline: 0;
}

.loading {
  width: 5%;
  margin-top: 140px;
}

.img_container {
  width: 1140px;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-template-rows: auto auto auto auto;
  grid-gap: 20px;
  grid-auto-flow: row;
}

.img_container .item {
  width: 270px;
  background: #154e3c;
  position: relative;
}

.img_container .item .overlay {
  position: absolute;
  background: rgb(0 0 0 / 0.8);
  width: 100%;
  height: 100%;
  top: 0;
  color: #fff;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.img_container .item .overlay .name {
  margin-bottom: 10px;
}

.img_container .item .avatar {
  width: 100%;
  height: 100%;
}

.btn {
  font-size: 14px;
  background-color: #fff;
  padding: 5px 10px;
  border-radius: 2px;
  outline: none;
  border: 0;
  cursor: pointer;;
}

.row {
  display: flex;
  align-items: center;
  width: 1140px;
  justify-content: space-between;
  margin-bottom: 40px;
}

.filtered {
  display: flex;
  align-items: center;
  width: 220px;
  justify-content: space-between;
}

.filtered h5 {
  color: #fff;
  margin-right: 10px;
}
